@import "_var.scss";


 .grid-container {
 	display: grid;
	max-width: 1103px;
 	margin:0 auto;
 }

.empty-section {
	width: 100%;
	height: 200%;
	background-color: #18191A;
}

.main {
	position: fixed;
	top: 207px;
	width: 100%;
	height: 100%;
	display: grid;
	background-color: #18191A;
	align-items: center;
	z-index: 5;
    .main__wrapper {
		display: grid;
		margin: 0 auto;
		max-width: 1600px;
		padding-left: 250px;
		& .container {
			display: grid;
			grid-template-columns:  1fr 2fr;
			padding: 70px 0;
			& .right {
				display: grid;
				justify-items: end;
				position: relative;

				& .board {
					position: absolute;
					left: 70px;
					top: 0;
					bottom: 0;
					display: grid;
					justify-items: center;
					align-items: center;
					& img {
						z-index: 1;
						transform: scale(1.2);
						position: absolute;
						left: 120px;
						bottom: 150px;
					}
					& .board-2 {
						opacity: 0;
					}
					& .board-3 {
						opacity: 0;
					}
					& .board-4 {
						opacity: 0;
					}
				}
				& .phone {
					opacity: 0.5;
				}
			}
			& .left {
				display: grid;
				& .left__inner {

				}
				&__article {
					& h1 {
						font-family: 'SF UI Display', serif;
						font-style: normal;
						font-weight: bold;
						font-size: 36px;
						line-height: 43px;
						color: #FFFFFF;
						width: 117%;
					}
				}
				&__text {
					& p {
						font-family: 'SF UI Display', serif;
						font-style: normal;
						font-weight: normal;
						font-size: 20px;
						line-height: 148.84%;
						letter-spacing: -0.5px;
						width: 117%;
						color: #FFFFFF;
					}
				}
				&--btn {
					width: 136px;
					height: 40px;
					background: #FF6600;
					border-radius: 4px;
					display: grid;
					& a {
						width: 100%;
						height: 100%;
						display: grid;
						justify-items: center;
						align-items: center;
						font-family: "SF UI Display", serif;
						font-style: normal;
						font-weight: 600;
						font-size: 17px;
						line-height: 20px;
						letter-spacing: -0.5px;
						color: white;
					}
				}
			}
		}
	}
}

@media only screen and (max-width : 1600px) {
    .main {
		height: auto;
		& .main__wrapper {
			& .container {
				padding: 50px 0;
				& .right {
					justify-items: center;
					& .right__wrapper{
						position: relative;
						width: 500px;
					}
					& .phone{
						width: 100%;
					}
					& .board {
						& img {
							transform: scale(0.9);
							left: -74px;
							bottom: 91px;
						}
					}
				}
				& .left__text {
					& p {
						font-size: 16px;
						width: 120%;
					}
				}
				& .left__article {
					 & h1 {
						 font-size: 32px;
					 }
				}
			}
		}
	}
}




.menu--active {
	color: var(--main-color) !important;
}
.element {
	width: 100%;
	height: 200vh;
	background-color: #18191A;
	display: grid;
	justify-items: center;
	align-items: start;
	overflow: hidden;
	position: relative;
	& .trigger {
		top: 0;
		position: absolute;
	}
	& img {
		z-index: 10;
		position: relative;
	}
}


.signal {
	width: 100%;
	height: 80%;
	display: grid;
	align-items: center;
	background-color: #5D3CB1;
	position: fixed;
	z-index: 10;
	bottom: -100%;
	& .signal__wrapper {
		display: grid;
		margin: 0 auto;
		max-width: 1600px;
		padding-left: 250px;
		& .container {
			display: grid;
			grid-template-columns:  1fr 2fr;
			padding: 70px 0;
			& .left {
				display: grid;
				&__article {
					& h1 {
						font-family: 'SF UI Display', serif;
						font-style: normal;
						font-weight: bold;
						font-size: 36px;
						line-height: 43px;
						color: #FFFFFF;
					}
				}
				&__text {
					& p {
						font-family: 'SF UI Display', serif;
						font-style: normal;
						font-weight: normal;
						font-size: 20px;
						line-height: 148.84%;
						letter-spacing: -0.5px;
						color: #FFFFFF;
					}
				}

				& .content__inner {
					& .up-menu {
						margin: 0;
						padding: 0;
						& li {
							list-style-type: none;
							& a {
								font-family: SF UI Display, serif;
								font-style: normal;
								font-weight: 600;
								font-size: 18px;
								line-height: 202.34%;
