								color: #FFFFFF;
							}
						}
					}
				}
			}
			& .right {
				display: grid;
				justify-items: center;
				position: relative;
				& .item {
					position: absolute;
					width: 230px;
					height: 520px;
					&__element {
						//position: absolute;
						width: 230px;
						height: 520px;
						background: #FFFFFF;
						border-radius: 10px;
						box-shadow: 0 50px 40px rgba(0, 0, 0, 0.25);
						position: relative;
						& .element__background {
							width: 230px;
							height: 248.33px;
							background: #F4F2F2;
							border-radius: 10px;
							display: grid;
							justify-items: center;
							align-items: center;
							& .background__arrow {
								width: 23.65px;
								height: 23.65px;
								background: #FFFFFF;
								border-radius: 10px;
								position: absolute;
								display: grid;
								justify-items: center;
								align-items: center;
								left: 20px;
								top: 10px;
							}
						}
						& .element__content {
							display: grid;
							grid-template-columns: 1fr 1fr;
							justify-items: center;
							& p {
								font-family: 'SF UI Display', serif;
								font-style: normal;
								font-weight: bold;
								font-size: 18px;
							}
						}
					}
					&__form {
						position: absolute;
						width: 347px;
						height: 167px;
						background: #FFFFFF;
						box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3), 0px -10px 30px rgba(255, 255, 255, 0.3);
						border-radius: 17px;
						padding: 10px;
						top: 60%;
						right: 10%;
						& .input__wrapper {
							position: relative;
							width: 100%;
							padding-top: 10px;
						}
						& input {
							width: 100%;
							height: 60px;
							border-radius: 5px;
							border: 4px solid #C0C0C0;
							outline: none;
							&::placeholder {
								color: #7A7A7A;
								font-family: 'SF UI Display', serif;
								font-style: normal;
								font-weight: bold;
								font-size: 20px;
							}
						}
						& input[type="text"] {
							padding-left: 50px;
							color: #7A7A7A;
							font-family: 'SF UI Display', serif;
							font-style: normal;
							font-weight: bold;
							font-size: 20px;
						}
						& .input__text {
							color: #7A7A7A;
							font-family: 'SF UI Display', serif;
							font-style: normal;
							font-weight: normal;
							font-size: 20px;
							background-color: #FFFFFF;
							position: absolute;
							top: 2px;
							line-height: 0.7;
							left: 10px;
							padding: 0 10px;
						}
						& .input__card {
							position: absolute;
							top: 30px;
							left: 20px;
						}
						& .block__wrapper {
							width: 100%;
							display: grid;
							grid-template-columns: 1fr 1fr 1fr;
							margin-top: 10px;
							grid-gap: 10px;
							& .block__item {
								border: 2px solid black;
								border-radius: 5px;
								display: grid;
								justify-items: center;
								align-items: center;
								padding: 5px 0;

							}
						}
					}
					&__form-1{
						width: 347px;
						height: 167px;
						background: #FFFFFF;
						box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3), 0px -10px 30px rgba(255, 255, 255, 0.3);
						border-radius: 17px;
						padding: 10px;
						top: 60%;
						right: 10%;
						position: absolute;
						opacity: 0;
						& .input__wrapper {
							position: relative;
							width: 100%;
							margin-top: 10px;
							height: 60px;
							border-radius: 5px;
							border: 4px solid #74E300;
							outline: none;
							& input {
								width: 100%;
								height: 100%;
								border-radius: 5px;
								border: none;
								outline: none;
							}
							& input[type="text"] {
								padding-left: 50px;
								color: black;
								font-family: "SF UI Display", serif;
								font-style: normal;
								font-weight: normal;
								font-size: 22px;
								letter-spacing: 4.1px;
							}
							& .input__card{
								position: absolute;
								top: 13px;
								left: 10px;
							}
							& .input__text {
								color: #7A7A7A;
								font-family: "SF UI Display", serif;
								font-style: normal;
								font-weight: normal;
								font-size: 11px;
								background-color: #FFFFFF;
								position: absolute;
								top: -5px;
								line-height: 0.7;
								left: 5px;
								padding: 0 10px;
								text-transform: uppercase;
							}
							& .input__card-date {
								opacity: 0;
								color: #7A7A7A;
								font-family: "SF UI Display", serif;
								font-style: normal;
								font-weight: normal;
								font-size: 11px;
								background-color: #FFFFFF;
								position: absolute;
								top: -5px;
								line-height: 0.7;
								left: 50%;
								padding: 0 10px;
								text-transform: uppercase;
							}
							& .input__card-cvv {
								opacity: 0;
								color: #7A7A7A;
								font-family: "SF UI Display", serif;
								font-style: normal;
								font-weight: normal;
								font-size: 11px;
								background-color: #FFFFFF;
								position: absolute;
								top: -5px;
								line-height: 0.7;
								right: 15px;
								padding: 0 10px;
								text-transform: uppercase;
							}
						}
						& .button__wrapper {
							display: grid;
							grid-template-columns: 1fr 4fr;
							height: 60px;
							margin-top: 10px;
							grid-gap: 5px;
							& .left--btn {
								background: #74E300;
								border-radius: 9px 0 0 9px;
								display: grid;
								justify-items: center;
								align-items: center;
							}
							& .right--btn {
								background: #97CF5E;
								border-radius: 0 9px 9px 0;
								display: grid;
								justify-items: center;
								align-items: center;
								& .btn__title {
									color: white;
									font-family: "SF UI Display", serif;
									font-weight: 500;
									font-size: 17px;
									line-height: 20px;
								}
							}
						}
						& .button-request {
							display: grid;
							width: 100%;
							height: 60px;
							background: #74E300;
							margin-top: 10px;
							border-radius: 9px;
							justify-items: center;
							align-items: center;
							color: white;
							font-family: "SF UI Display", serif;
							font-style: normal;
							font-weight: normal;
							font-size: 20px;
							opacity: 0;
						}
						& .payment-accept {
							display: grid;
							justify-items: center;
							opacity: 0;
							& img {
								width: 50px;
							}
							& p {
								margin: 0;
								color: #5D3CB1;
								font-family: SF UI Display, serif;
								font-style: normal;
								font-weight: 500;
								font-size: 12px;
								line-height: 14px;

							}
						}
					}
				}
				& .item-1 {
					position: absolute;
					width: 230px;
					height: 520px;
					display: grid;
					align-items: center;
					opacity: 0;
					& .item__form-2{
						width: 347px;
						height: 167px;
						background: #FFFFFF;
						box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3), 0px -10px 30px rgba(255, 255, 255, 0.3);
						border-radius: 17px;
						padding: 10px;
						& .input__wrapper {
							position: relative;
							width: 100%;
							margin-top: 10px;
							height: 60px;
							border-radius: 5px;
							border: 4px solid #74E300;
							& input {
								width: 100%;
								height: 100%;
								border-radius: 5px;
								border: none;
								outline: none;
							}
							& input[type="text"] {
								padding-left: 50px;
								color: black;
								font-family: "SF UI Display", serif;
								font-style: normal;
								font-weight: normal;
								font-size: 22px;
								letter-spacing: 4.1px;
							}
							& .input__card{
								position: absolute;
								top: 13px;
								left: 10px;
							}
							& .input__text {
								color: #7A7A7A;
								font-family: "SF UI Display", serif;
								font-style: normal;
								font-weight: normal;
								font-size: 10px;
								background-color: #FFFFFF;
								position: absolute;
								top: -5px;
								line-height: 0.7;
								left: 5px;
								padding: 0 10px;
								text-transform: uppercase;
							}
							& .input__card-date {
								color: #7A7A7A;
								font-family: "SF UI Display", serif;
								font-style: normal;
								font-weight: normal;
								font-size: 10px;
								background-color: #FFFFFF;
								position: absolute;
								top: -5px;
								line-height: 0.7;
								left: 50%;
								padding: 0 10px;
								text-transform: uppercase;
								opacity: 0;
							}
							& .input__card-cvv {
								color: #7A7A7A;
								font-family: "SF UI Display", serif;
								font-style: normal;
								font-weight: normal;
								font-size: 10px;
								background-color: #FFFFFF;
								position: absolute;
								top: -5px;
								line-height: 0.7;
								right: 15px;
								padding: 0 10px;
								text-transform: uppercase;
								opacity: 0;
							}
						}
						& .button__wrapper {
							display: grid;
							grid-template-columns: 1fr 4fr;
							height: 60px;
							margin-top: 10px;
							grid-gap: 5px;
							& .left--btn {
								background: #74E300;
								border-radius: 9px 0 0 9px;
								display: grid;
								justify-items: center;
								align-items: center;
							}
							& .right--btn {
								background: #97CF5E;
								border-radius: 0 9px 9px 0;
								display: grid;
								justify-items: center;
								align-items: center;
								& .btn__title {
									color: white;
									font-family: "SF UI Display", serif;
									font-style: normal;
									font-weight: normal;
									font-size: 20px;
								}
							}
						}
						& .button-request {
							display: grid;
							width: 100%;
							height: 60px;
							background: #74E300;
							margin-top: 10px;
							border-radius: 9px;
							justify-items: center;
							align-items: center;
							color: white;
							font-family: "SF UI Display", serif;
							font-style: normal;
							font-weight: normal;
							font-size: 20px;
						}
						& .payment-accept {
							display: grid;
							justify-items: center;
							& img {
								width: 50px;
							}
							& p {
								margin: 0;
								color: #5D3CB1;
								font-family: SF UI Display, serif;
								font-style: normal;
								font-weight: 500;
								font-size: 12px;
								line-height: 14px;
							}
						}
						& .color__form {
							width: 347px;
							height: 60px;
							display: grid;
							grid-template-columns: repeat(6,1fr);
							padding: 10px;
							justify-items: center;
							position: absolute;
							left: 0;
							bottom: 50px;
							& .color-circle {
								width: 40px;
								height: 40px;
								border-radius: 50%;
								display: grid;
								justify-items: center;
								align-items: center;
								& img {
									opacity: 0;
								}
							}
							& .color-circle:nth-child(1){
								background-color: #F93131;
							}
							& .color-circle:nth-child(2){
								background-color: #74E300;
							}
							& .color-circle:nth-child(3){
								background-color: #FB0BC6;
							}
							& .color-circle:nth-child(4){
								background-color: #FFD80B;
							}
							& .color-circle:nth-child(5){
								background-color: #0059DE;
							}
							& .color-circle:nth-child(6){
								background-color: #1E192C;
							}
							& .color-hand {
								position: absolute;
								left: 0;
								bottom: -33px;
							}
						}
					}
				}
			}
		}
	}
}


.anchor-animation {
	width: 100%;
	height: auto;
	background-color: #5D3CB1;
	& .anchor-block {
		height: 200px;
	}
}
.errors {
	display: none;
}
@media only screen and (max-width : 1550px) {
   .signal {
	   align-items: initial;
   }
	.signal .signal__wrapper .container .left__text p{
		font-size: 16px;
	}
}

@media only screen and (max-width : 1400px) {
    .errors {
		display: grid;
		width: 100%;
		height: 100vh;
		align-items: center;
		justify-items: center;
		& p {
			font-size: 70px;
		}
	}
	.signal {
		display: none;
	}
	body {
		overflow-y: hidden;
	}
	.main {
		display: none;
	}
	.empty-section {
		display: none;
	}
	.element {
		display: none;
	}
	.anchor-animation{
		 display: none;
	}
	.header {
		display: none;
	}
}